import {useState} from 'react';

import Separator from './Separator';
import MainButton from './MainButton';
import Loading from './Loading';
import {createUserWithPhoneNumber, verifyCode} from './apiService';

const PHONE_STAGE = 'phone_stage';
const CODE_STAGE = 'code_stage';

export default ({page, setPage}) => {
    const [phoneNumber, setPhoneNumber] = useState('');
    const [code, setCode] = useState('');
    const [name, setName] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [stage, setStage] = useState(PHONE_STAGE);
    const [enableErrorMessage, setEnableErrorMessage] = useState(false);

    const className = `page modal sign-in-page ${page === 'sign-in' ? 'visible' : ''}`;

    const handleClose = () => {
        setPhoneNumber('');
        setCode('');
        setStage(PHONE_STAGE);
        setEnableErrorMessage(false);

        setPage('app');
    };

    const handlePhoneNumberInputChange = (e) => {
        setPhoneNumber(e.target.value);
    }

    const handleCodeInputChange = (e) => {
        setCode(e.target.value);
    }

    const handleNameChange = (e) => {
        setName(e.target.value);
    }

    const handleSignInClick = () => {
        setIsLoading(true);
        if (stage === PHONE_STAGE) {
            createUserWithPhoneNumber(phoneNumber).then((confirmationResult) => {
                setIsLoading(false);
                setStage(CODE_STAGE);
            });
        } else if (stage === CODE_STAGE) {
            verifyCode(code, name).then(user => {
                setIsLoading(false);
                setPage('app');

                setPhoneNumber('');
                setCode('');
                setStage(PHONE_STAGE);
                setEnableErrorMessage(false);
            }).catch(e => {
                console.log('e', e);
                setIsLoading(false);
                setCode('');
                setEnableErrorMessage(true);
            });
        }
    }

    return (
        <div className={className}>
            <div className="page-header">
                <span></span>
                <div onClick={handleClose}>x</div>
            </div>

            {page === 'sign-in' &&
                <div className="modal-body">
                    <div className="section-container">
                        <div className="title">
                            <span>{` `}</span>
                        </div>
                        <Separator emoji={""} />

                        <input value={name} className="input" type="text" placeholder=" " onChange={handleNameChange} />
                        {stage === PHONE_STAGE ?
                            <>
                                <input value={phoneNumber} className="input" type="text" placeholder=" " onChange={handlePhoneNumberInputChange} />
                            </>
                            :
                            <input value={code} className="input" type="text" placeholder="  ?" onChange={handleCodeInputChange} />
                        }
                        {enableErrorMessage && <div className="error-message">   ? / !</div>}
                        {isLoading ?
                            <Loading text={stage === PHONE_STAGE ? " ..." : "  ..."} />
                            :
                            <MainButton content={stage === PHONE_STAGE ? " " : ""} onClick={handleSignInClick} />
                        }
                    </div>
                </div>
            }
        </div>
    )
}
